import { Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/vue';
import foodListData from '@/assets/datas/foodListData.json';
export default {
  components: {
    Swiper,
    SwiperSlide
  },
  setup() {
    return {
      modules: [Autoplay]
    };
  },
  data() {
    return {
      swiper: null,
      swiperOption: {
        slidesPerView: 4,
        spaceBetween: 0,
        loop: true,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false
        },
        speed: 1000,
        breakpoints: {
          1: {
            slidesPerView: 1,
            spaceBetween: 0
          },
          576: {
            slidesPerView: 2,
            spaceBetween: 0
          },
          780: {
            slidesPerView: 3,
            spaceBetween: 0
          },
          990: {
            slidesPerView: 4,
            spaceBetween: 0
          }
        }
      },
      foodListDataMeal: foodListData.meal,
      foodListDataSide: foodListData.side,
      mealTitleBgColor: ['#aa8343cb', '#6b5128cb', '#C1625Dcb', '#A7372Bcb', '#2b1f14cb']
    };
  },
  methods: {
    onSwiper(swiper) {
      this.swiper = swiper;
      // console.log(swiper);
    },

    prevFn() {
      // console.log('prevFn',this.swiper);
      this.swiper.slidePrev();
    },
    nextFn() {
      // console.log('nextFn',this.swiper);
      this.swiper.slideNext();
    }
  }
};